    color: #9af089;
    -webkit-animation: fadein-content 4s;
            animation: fadein-content 4s;
}

/* Fade out */

.title-fade{
    color: #9af089;
    -webkit-animation: fadeout-content 1s;
            animation: fadeout-content 1s;
    opacity: 0;
}


@-webkit-keyframes fadein-content {
    from {opacity: 0;}
    75% { opacity: 0;}
    to {opacity: 1;}
}


@keyframes fadein-content {
    from {opacity: 0;}
    75% { opacity: 0;}
    to {opacity: 1;}
}

@-webkit-keyframes fadeout-content {
    from {opacity: 1;}
    75% { opacity: 1;}
    to {opacity: 0;}
}

@keyframes fadeout-content {
    from {opacity: 1;}
    75% { opacity: 1;}
    to {opacity: 0;}
}
